/* montserrat-700 - cyrillic */
@font-face {
  font-family: "Montserrat";
  font-style: normal;
  font-weight: 700;
  src: local(""),
    url("../fonts/montserrat/montserrat-v18-cyrillic-700.woff2") format("woff2"),
    url("../fonts/montserrat/montserrat-v18-cyrillic-700.woff") format("woff");
  font-display: swap;
}

